import React from "react";

import { FaPhoneAlt, FaEnvelope, FaMapMarkerAlt } from "react-icons/fa";

const index = () => {
  const contactList = [
    {
      id: 1,
      title: "Call",
      icon: "FaPhoneAlt",
      value: "9866010213",
    },
    {
      id: 2,
      title: "Mail",
      icon: "FaEnvelope",
      value: "niranjanadhikari2053@gmail.com",
    },
    {
      id: 3,
      title: "Address",
      icon: "FaMapMarkerAlt",
      value: "Tikathali - 05, Lalitpur, Nepal",
    },
  ];

  return (
    <div className="flex space-x-6 justify-between pr-16 py-16">
      {contactList.map(({ id, title, icon, value }) => {
        return (
          <div className="flex" key={id}>
            <div className="bg-gray-800 text-white rounded-2xl p-4">
              <FaPhoneAlt />
            </div>
            <div className="ml-2 font-medium">
              <p className="leading-5">{title}</p>
              <p className>{value}</p>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default index;
