import React from "react";
import Title from "../../components/Title";

import Skill from "./skill";

import graphicdesign from "../../images/graphicdesign.svg";
import UXDesigner from "../../images/uiuxdesign.svg";
import UIDev from "../../images/uidev.svg";

const jobRoles = [
  {
    id: 1,
    jobTitle: "Graphic Designer",
    jobIcon: graphicdesign,
    jobSkills: [
      "Logo Design",
      "Social media ads",
      "Print Design",
      "Packaging Design",
    ],
  },
  {
    id: 2,
    jobTitle: "UI/UX Designer",
    jobIcon: UXDesigner,

    jobSkills: [
      "Wireframing",
      "Usecase research",
      "Style guide design",
      "Prototyping",
      "UI Design(mobile & web)",
    ],
  },
  {
    id: 1,
    jobTitle: "UI/UX Developer",
    jobIcon: UIDev,
    jobSkills: [
      "HTML/CSS/SCSS",
      "Tailwind",
      "Bootstrap",
      "StyledComponents",
      "Javascript/es6/Jquery",
      "React/Gatsby",
      "Github",
    ],
  },
];

const index = () => {
  return (
    <div className="py-16">
      <Title title="My role so far" />
      <div className="grid grid-cols-3 gap-4 mt-12">
        {jobRoles.map((role) => {
          const { id, jobTitle, jobIcon, jobSkills } = role;
          return (
            <div className="text-center">
              <img
                src={jobIcon}
                className="w-20 h-20 object-contain d-block mx-auto mb-3"
                alt={`icon defining ${jobTitle}`}
              />
              <h3 className="font-medium text-2xl">{jobTitle}</h3>

              <Skill jobSkills={jobSkills} />
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default index;
