import React from "react";
import Title from "../Title";

import blankCompany from "../../images/blankCompany.svg";

const jobExperience = [
  {
    id: "1",
    role: "UX Designer",
    company: "Optimum Futurist",
    companyLogo:
      "https://media-exp1.licdn.com/dms/image/C4D0BAQEy60nl7vJ4IA/company-logo_100_100/0/1609958980130?e=1645660800&v=beta&t=aKx9DTUKDtUc8UgmS04_Z6nCvcz-zBhCIE_rdjOxxY0",
    from: "Mar 2021",
    to: "present",
    type: "Full-time",
    duration: "9 mos+",
  },
  {
    id: "2",
    role: "UI Developer",
    company: "Bentray Technologies",
    companyLogo:
      "https://media-exp1.licdn.com/dms/image/C560BAQHJMBV1Ai2_9Q/company-logo_100_100/0/1519888978535?e=1645660800&v=beta&t=YHbcC1Sto3xylL7t7ZsjDOBQtoTnYq8ag2Lkvc2TD9o",
    from: "Jan 2020",
    to: "Feb 2021",
    type: "Full-time",
    duration: "1yr 2 mos",
  },
  {
    id: "3",
    role: "Graphic Designer",
    company: "Digital Ghumti",
    companyLogo:
      "https://media-exp1.licdn.com/dms/image/C560BAQFrPLdN-6NFlw/company-logo_100_100/0/1588853092675?e=1645660800&v=beta&t=rdWBRABcANXLE8_BvLEfMF-VzdpKutm8zDrcETvzLRo",
    from: "May 2020",
    to: "Jul 2020",
    type: "Part-time",
    duration: "3 mos",
  },
  {
    id: "4",
    role: "UI Designer",
    company: "Metashrine",
    companyLogo: "",
    from: "May 2019",
    to: "Jul 2019",
    type: "Part-time",
    duration: "3 mos",
  },
  {
    id: "5",
    role: "Graphic Designer",
    company: "Perfect creation",
    companyLogo: "",
    from: "Feb 2019",
    to: "May 2019",
    type: "Intern",
    duration: "4 mos",
  },
];

const index = () => {
  return (
    <div className="py-16">
      <Title title="Companies I've worked for" />
      <div className="mt-10">
        <div className="grid grid-cols-3 gap-4">
          {jobExperience.map(
            ({ id, role, company, from, to, duration, type, companyLogo }) => (
              <div key={id} className="shadow rounded-md p-4">
                {companyLogo ? (
                  <img
                    className="w-10 h-10 rounded-md mb-2"
                    src={companyLogo}
                    alt={`logo of ${company}`}
                  />
                ) : (
                  <img
                    className="w-10 h-10 rounded-md mb-2"
                    src={blankCompany}
                    alt="placeholder company"
                  />
                )}

                <p className="font-medium text-2xl">{role}</p>
                <p className="text-base text-gray-600">
                  {`${company} | ${type}`}
                </p>
                <p className="text-sm text-gray-400">
                  {`${from} - ${to} ⌛ ${duration}`}
                </p>
              </div>
            )
          )}
        </div>
      </div>
    </div>
  );
};

export default index;
