import React from "react";

import photo from "../../images/niranjan.png";

const index = () => {
  return (
    <div class="container mx-auto flex items-center pt-10">
      <div className="">
        <img src={photo}  className="object-cover" />
      </div>
      <div className="mx-16">
        <div className="profile">
          <h1 className="text-6xl font-bold">
            <p className="text-4xl font-medium text-gray-700 mb-2">Hi, I'm 👋</p>
            Niranjan Adhikari
          </h1>
          <p className="text-2xl text-gray-400 mt-2">UI/UX Designer</p>
        </div>
        <p className="mt-5 text-gray-500 text-base pr-8">
          I am a creative designer with a passion for solving business problems.
          I love good design, coffee, movies and design discussions. I aspire to
          create solutions that make an Impact in the society we live in.
        </p>
      </div>
    </div>
  );
};

export default index;
